import React from 'react';
import { Link } from 'react-router-dom';
import { useCart } from '../hook/useCart'

function Header(props) {
    const { totalPrice } = useCart();

    return (
        <header className="d-flex justify-between align-center p-40">
            <Link to="/">
                <div className="d-flex align-center">
                    <img width={80} height={80} src="img/kt5.png" alt="Logotype" />
                    <div>
                        <h3 className="text-uppercase">Spices from all over the world</h3>
                        <p className="opacity-5">The amazing world of spices</p>
                    </div>
                </div>
            </Link>
            <ul className="d-flex">
                <li onClick={props.onClickCart} className="mr-30 cu-p">
                    <img width={30} height={30} src='img/sop.svg' alt="cart"/>
                    <span>{totalPrice}$</span>
                </li>
                <li className="mr-20 cu-p">
                    <Link to="/favorites">
                        <img width={30} height={30} src='img/lov.svg'alt="favorites" />
                    </Link>
                </li>
                <li className="mr-20 cu-p">
                    <Link to="/orders">
                        <img width={30} height={30} src='img/ava.svg' alt="login"/>
                    </Link>
                </li>
            </ul>
        </header>
    );
}

export default Header;